import { Paper, Table, TableContainer } from "@material-ui/core";
import Container from "@material-ui/core/Container";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import _ from "lodash";
import { Pokedex } from "pokeapi-js-wrapper";
import React, { useEffect, useState } from "react";
import "./App.css";
import Loading from "./components/common/Loading.jsx";
import NavBar from "./components/common/NavBar.jsx";
import EvolutionChain from "./components/EvolutionChain.jsx";
import MoveList from "./components/moves/MoveList.jsx";


const P = new Pokedex();

const App = () => {
	const [searchValue, setSearchValue] = useState("");
	const [pokemonName, setPokemonName] = useState("");
	const [pokemon, setPokemon] = useState(null);
	const [version, setVersion] = useState("platinum");
	const [moves, setMoves] = useState([]);
	const [species, setSpecies] = useState(null);

	useEffect(() => {
		(async () => {
			const pokemon = await P.getPokemonByName(pokemonName);
			setPokemon(pokemon);

			const species = await P.resource(pokemon.species.url);
			setSpecies(species);
		})();
	}, [pokemonName]);

	useEffect(() => {
		if (!pokemon) return;

		const versionMoves = [];

		for (let move of pokemon.moves) {
			const version_group_detail = move.version_group_details.find(vgd => vgd.version_group.name === version);
			if (!version_group_detail) continue;

			const { version_group_details, ...moveSimple } = move;

			versionMoves.push({
				...moveSimple,
				version_group_detail
			});
		}

		setMoves(versionMoves);
	}, [pokemon, version]);

	if (!pokemonName) setPokemonName("pikachu");

	if (!pokemon) return <Loading />;

	const handlEvolutionChainClick = (name) => {
		setPokemonName(name);
	};

	const handleSearchSumit = event => {
		event.preventDefault();
		setPokemonName(searchValue);
	};

	const handleSearchChange = event => {
		setSearchValue(event.target.value);
	};

	return (
		<div className="app">
			<NavBar onSubmit={handleSearchSumit} onSearchChange={handleSearchChange} />
			<Container maxWidth="xl">
				<div className="content">
					<div className="menu-left-area">
						<Paper className="menu-left" elevation={7}>
							<h2>{_.startCase(pokemon.name)}</h2>
							<img src={pokemon.sprites.front_default} alt={pokemon.name} />
							<div>
								{pokemon.types.map(pokemonType => (
									<div key={pokemonType.type.name}>{pokemonType.type.name}</div>
								))}
							</div>
						</Paper>
					</div>
					<TableContainer className="stats" component={Paper} elevation={1}>
						<Table size="small">
							<TableBody>
								{pokemon.stats.slice().map(pokemonStat => (
									<TableRow key={pokemonStat.stat.name}>
										<TableCell variant="head">{pokemonStat.stat.name === "hp" ? "HP" : _.startCase(pokemonStat.stat.name)}</TableCell>
										<TableCell>{pokemonStat.base_stat}</TableCell>
									</TableRow>
								))}
							</TableBody>
						</Table>
					</TableContainer>
					<EvolutionChain species={species} onClick={handlEvolutionChainClick} />
					<MoveList moves={moves} />
				</div>
			</Container>
		</div>
	);
};

export default App;
