import { Paper, TableContainer } from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import _ from "lodash";
import React from "react";
import Loading from "../common/Loading.jsx";
import MoveItem from "./MoveItem.jsx";


const MoveList = ({ moves }) => {
	if (!moves) return <Loading />;

	moves.sort((m1, m2) => m1.version_group_detail.level_learned_at - m2.version_group_detail.level_learned_at);
	const sortedMoves = _.orderBy(moves, ["version_group_detail.move_learn_method.name", "version_group_detail.level_learned_at", ["desc", "asc"]]);

	return (
		<TableContainer className="movelist" component={Paper}>
			<Table size="small" stickyHeader>
				<TableHead>
					<TableRow>
						<TableCell>Level</TableCell>
						<TableCell>Name</TableCell>
						<TableCell>Type</TableCell>
						<TableCell>Category</TableCell>
						<TableCell>Power</TableCell>
						<TableCell>Accuracy</TableCell>
						<TableCell>PP</TableCell>
					</TableRow>
				</TableHead>
				<TableBody>
					{sortedMoves.map(pokemonMove => (
						<MoveItem key={pokemonMove.version_group_detail.move_learn_method.name + pokemonMove.move.name} pokemonMove={pokemonMove} />
					))}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

export default MoveList;