import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome";
import Paper from "@material-ui/core/Paper";
import _ from "lodash";
import { Pokedex } from "pokeapi-js-wrapper";
import React, { useEffect, useState } from "react";
import Loading from "./common/Loading.jsx";


const P = new Pokedex();

const EvolutionChain = ({ species, onClick }) => {
	const [evolutionChain, setEvolutionChain] = useState(null);

	useEffect(() => {
		if (!species) return;

		(async () => {
			const evolutionChain = await P.resource(species?.evolution_chain.url);
			setEvolutionChain(evolutionChain);
		})();
	}, [species]);

	const getEvolutionConditions = (evolution) => {
		console.log(evolution);
		const conditions = [];

		let i = 0;
		for (const details of evolution.evolution_details) {
			const trigger = details.trigger.name;

			let triggers = "";
			if (trigger === "level-up") {
				if (details.min_level) triggers += `Level ${details.min_level}\n`;
				if (details.min_happiness) triggers += " Hapiness\n";
				if (details.known_move_type) triggers += `Know ${_.startCase(details.known_move_type.name)}-type move\n`;
				if (details.min_affection) triggers += `Affection: ${details.min_affection}+\n`;
				if (details.time_of_day) triggers += `During the ${details.time_of_day}\n`;
				if (details.location) triggers += `Level up in ${_.startCase(details.location.name)}\n`;
				if (details.min_beauty) triggers += `Beauty\n`;
				if (details.relative_physical_stats !== null) {
					const relativePhysicalMapping = {
						"-1": "<",
						"0": "=",
						"1": ">"
					};
					triggers += `Atk ${relativePhysicalMapping[details.relative_physical_stats]} Def`;
				}
			} else if (trigger === "use-item") {
				triggers = _.startCase(details.item.name);
			} else if (trigger === "trade") {
				triggers = "Trade\n";
				if (details.held_item) triggers += `Holding ${_.startCase(details.held_item.name)}\n`;
			} else if (trigger === "shed") {
				triggers = `Evolve with a pokéball in the bag and space in the party`;
			}
			conditions.push(triggers || `Evolution${i++}`);
		}

		return _.uniq(conditions).join("<hr />");
	};

	const renderEvolutionTree = chain =>
		<div className="evo">
			<Paper className="evo__main" onClick={() => onClick(chain.species.name)}>{chain.species.name}</Paper>
			{chain.evolves_to.length > 0 &&
			<div className="evo__evos">
				{chain.evolves_to.map(evolution =>
					<div className="evos__evo" key={evolution.species.name}>
						<div className="evo__condition">
							<pre className="evo__requirement" dangerouslySetInnerHTML={{ __html: getEvolutionConditions(evolution) }} />
							<FA icon="arrow-right" style={{ alignSelf: "center" }} />
						</div>
						{renderEvolutionTree(evolution)}
					</div>
				)}
			</div>}
		</div>;

	return (
		<Paper className="evolution">
			<div className="evolution__content">
				{evolutionChain ? renderEvolutionTree(evolutionChain.chain) : <Loading />}
			</div>
		</Paper>
	);
};

export default EvolutionChain;