import TableCell from "@material-ui/core/TableCell/TableCell.js";
import TableRow from "@material-ui/core/TableRow";
import _ from "lodash";
import { Pokedex } from "pokeapi-js-wrapper";
import React, { useEffect, useState } from "react";

const P = new Pokedex();

const MoveItem = ({pokemonMove}) => {
	const [move, setMove] = useState(null);

	useEffect(() => {
		(async () => {
			setMove(await P.getMoveByName(pokemonMove.move.name))
		})();
	}, [pokemonMove.move.name]);

	if (!move) return <TableRow><TableCell colSpan={7}>Loading Move...</TableCell></TableRow>;

	const moveLearnMethod = pokemonMove.version_group_detail.move_learn_method.name;
	let learn = "";

	switch (moveLearnMethod) {
		case "level-up":
			learn = `Level ${pokemonMove.version_group_detail.level_learned_at}`;
			break;
		default:
			learn = _.startCase(pokemonMove.version_group_detail.move_learn_method.name);
	}

	return (
		<TableRow key={move.id}>
			<TableCell>{learn}</TableCell>
			<TableCell>{_.startCase(move.name)}</TableCell>
			<TableCell>{move.type.name}</TableCell>
			<TableCell>{move.damage_class.name}</TableCell>
			<TableCell>{move.power}</TableCell>
			<TableCell>{move.accuracy}</TableCell>
			<TableCell>{move.pp}</TableCell>
		</TableRow>
	);
};

export default MoveItem;